"use client";

import { useParams } from "next/navigation";

const blogContent: Record<string, any> = {
  "home-painting-renovation-guide": {
    title: "Transform Your Home: The Ultimate Guide to Painting & Renovation",
    content: `
      <h3>Step 1: Plan Your Design</h3>
      <p>Start with a clear theme — whether modern, classic, or luxury.</p>

      <h3>Step 2: Choose Quality Paint</h3>
      <p>Use washable, low-VOC paints for long-lasting and safe interiors.</p>

      <h3>Step 3: Hire Professionals</h3>
      <p>Expert painters and renovators ensure a flawless finish.</p>

      <hr />
      <p><strong>✨ Pro Tip:</strong> Always do a patch test for colors before applying them across your walls.</p>
    `,
  },
  "budget-friendly-home-makeovers": {
    title: "Top 5 Budget-Friendly Home Makeover Ideas",
    content: `
      <ol>
        <li>Repaint your walls with light, bright colors.</li>
        <li>Upgrade lighting fixtures for instant luxury.</li>
        <li>Add wall art or texture.</li>
        <li>Declutter and rearrange furniture.</li>
        <li>Add indoor plants for freshness.</li>
      </ol>
      <p>These simple steps make a big visual impact without heavy spending.</p>
    `,
  },
};

export default function BlogPost() {
  const { slug } = useParams();
  const post = blogContent[slug as string];

  if (!post) {
    return <div className="p-10 text-center text-gray-500">Blog not found.</div>;
  }

  return (
    <div className="max-w-3xl mx-auto py-20 px-6 md:px-0">
      <h1 className="text-4xl font-bold text-[#FE904E] mb-6">{post.title}</h1>
      <div
        className="prose prose-lg"
        dangerouslySetInnerHTML={{ __html: post.content }}
      />
    </div>
  );
}

/** ✅ Add this for static export support */
export async function generateStaticParams() {
  return [
    { slug: "home-painting-renovation-guide" },
    { slug: "budget-friendly-home-makeovers" },
  ];
}
